:root {
  /* Core palette (UCL-inspired) */
  --ucl-black: #000000;
  --ucl-ink:   #111111;
  --ucl-blue:  #005bbb; /* primary accent */
  --ucl-azure: #e9f2ff; /* light blue bg */
  --ucl-sky:   #f3f8ff; /* softer blue bg */
  --ucl-ink-2: #222;    /* body text */
  --muted:     #6b7280; /* gray-500-ish */
  --line:      #e5e7eb; /* gray-200-ish */
  --card:      #ffffff; /* surfaces */
  --bg:        #ffffff; /* page bg */
  --link:      var(--ucl-blue);

  /* Elevation / radii */
  --radius-sm: 6px;
  --radius-md: 12px;
  --radius-lg: 20px;

  /* Shadows */
  --shadow-0: 0 0 0 rgba(0,0,0,0);
  --shadow-1: 0 4px 14px rgba(0,0,0,.06);
  --shadow-2: 0 8px 24px rgba(0,0,0,.08);

  /* Focus */
  --focus: var(--ucl-blue);

  /* Gradients (for subtle borders/highlights) */
  --grad-blue: linear-gradient(180deg, #ffffff, #eef5ff);
  --grad-outline: linear-gradient(180deg, #cfe0ff, #eaf1ff);
}

/* Optional: auto-darken if system uses dark mode (keeps contrast strong) */
@media (prefers-color-scheme: dark) {
  :root {
    --bg:        #0d1116;
    --card:      #121821;
    --ucl-ink-2: #e5e7eb;
    --muted:     #a3aab5;
    --line:      #2a3340;
    --grad-blue: linear-gradient(180deg, #161b22, #0d1116);
    --grad-outline: linear-gradient(180deg, #1e2a3d, #15202b);
    --shadow-1:  0 4px 14px rgba(0,0,0,.35);
    --shadow-2:  0 8px 24px rgba(0,0,0,.45);
  }
}

/* Variables */
$maxw: 1100px;
$gap: 1rem;
$brand: #000;          /* UCL black */
$accent: #005bbb;      /* UCL blue-ish accent */
$grey-1: #f5f5f5;
$grey-2: #e5e5e5;
$text: #111;

/* Base */
* { box-sizing: border-box; }
html { scroll-behavior: smooth; }
body { color: var(--ucl-ink-2); background: var(--bg); }
a { color: var(--link); }
a:hover, a:focus { text-decoration: underline; }
img { max-width: 100%; height: auto; }

.skip-link {
  position: absolute; left: -9999px; top: auto;
  &:focus { left: 0; top: 0; background: #fff; color: #000; padding: .5rem 1rem; z-index: 10000; }
}

/* Banner */
.banner { border-bottom: 1px solid var(--line); }
.banner-top {
  display: grid;
  grid-template-columns: auto auto 1fr;
  align-items: center;
  gap: $gap;
  max-width: $maxw; margin: 0 auto; padding: .75rem $gap;
}
.logo img { height: 40px; width: auto; }
.team-name { font-weight: 800; letter-spacing: .08em; text-transform: uppercase; }
.announce-wrap { display: flex; justify-content: flex-end; }

/* LCD announcement */
.lcd {
  min-height: 40px;
  display: inline-flex; align-items: center; gap: .5rem;
  padding: .25rem .5rem;
  border-radius: 4px;
  background: linear-gradient(#dfe8d6, #c6d1b8);
  border: 1px solid #9fb08a;
  font-family: "Courier New", ui-monospace, monospace;
  letter-spacing: .04em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.lcd-cta { margin-left: .5rem; font-weight: 700; }

/* Nav */
.banner-nav { border-top: 1px solid var(--line); background: #f7f9fb; }
.nav-bar { max-width: $maxw; margin: 0 auto; }
.nav-root {
  list-style: none; margin: 0; padding: 0;
  display: flex; gap: $gap;
}
.nav-col { flex: 1 1 0; }


/* Footer */
.site-footer { border-top: 1px solid var(--line); }
.footer-inner { max-width: $maxw; margin: 0 auto; padding: 1rem $gap; display: flex; align-items: center; justify-content: space-between; gap: $gap; flex-wrap: wrap; }
.footer-links { list-style: none; margin: 0; padding: 0; display: flex; gap: 1rem; }

/* Layout */
.site-main { max-width: $maxw; margin: 1.5rem auto; padding: 0 $gap; }

@media (max-width: 900px) {
  .menu-panels { grid-template-columns: 1fr; }
  .banner-top { grid-template-columns: auto 1fr; }
  .team-name { display: none; } /* keep it simple on small screens, logo + LCD */
}

/* Menubar skeleton */
.nav-root {
  list-style: none; margin: 0; padding: 0;
  display: flex; gap: 1rem; align-items: stretch;
}
.nav-item { position: relative; }
.nav-link, .menu-button {
  display: inline-flex; align-items: center; justify-content: center;
  padding: .75rem 1rem; font-weight: 600; background: transparent; border: 0;
  cursor: pointer; text-decoration: none;
}
.menu-button:focus, .nav-link:focus { outline: 2px solid #000; outline-offset: 2px; }

/* Panels */
.menu-panel {
  position: absolute; left: 0; top: 100%;
  min-width: 14rem; margin: 0; padding: .5rem;
  list-style: none; background: #fff; border: 1px solid #e5e5e5; border-radius: 8px;
  box-shadow: 0 6px 18px rgba(0,0,0,.08);
  z-index: 1000;
}
.menu-panel [role="menuitem"] {
  display: block; padding: .5rem .75rem; border-radius: 6px; text-decoration: none;
}
.menu-panel [role="menuitem"]:hover,
.menu-panel [role="menuitem"]:focus {
  background: #f5f5f5; text-decoration: none;
}
.nav-link.disabled { color: #888; cursor: not-allowed; text-decoration: none; }

/* Keep panels inside on small screens */
@media (max-width: 900px) {
  .nav-root { flex-wrap: wrap; }
  .menu-panel { position: static; box-shadow: none; border-radius: 6px; margin-top: .25rem; }
}

/* ========= Alliances UI enhancements ========= */

/* --- 1) List reset + spacing utilities --- */
.list-plain { list-style: none; margin: 0; padding: 0; }
.cluster { display: flex; flex-wrap: wrap; gap: .5rem .75rem; }

/* Stack = vertical rhythm; space between direct children */
.stack > * + * { margin-top: var(--stack-gap, .75rem); }

/* --- 2) Pill links (e.g., service spotlights) --- */
.pill {
  display: inline-flex; align-items: center; gap: .4ch;
  padding: .3rem .6rem;
  border: 1px solid #d7e3f7;
  border-radius: 999px;
  background: var(--ucl-sky);
  color: var(--ucl-ink-2);
  text-decoration: none; font-size: .95rem; line-height: 1.2;
}
.pill:hover, .pill:focus {
  background: var(--ucl-azure);
  border-color: #c9dcf5;
  text-decoration: none;
  outline: 2px solid var(--focus);
  outline-offset: 2px;
}

/* Optional “soft” variant */
.pill--soft {
  background: #f7fafc;
  border-color: #e6edf5;
}

/* --- 3) Card tiles with hover lift --- */
.tile {
  position: relative;
  border-radius: var(--radius-md);
  border: 1px solid var(--line);
  background: var(--card);
  transition: transform .18s ease, box-shadow .18s ease, border-color .18s ease;
  box-shadow: var(--shadow-0);
}
.tile:hover,
.tile:focus-within {
  transform: translateY(-2px);
  border-color: #e0e7ff;
  box-shadow: var(--shadow-2);
}

/* Fancy gradient-border variant (optional) */
.tile--gradient {
  position: relative;
  background: var(--card);
  border: 1px solid transparent;
  background-image:
    var(--grad-blue),                   /* inner */
    var(--grad-outline);                /* border */
  background-origin: border-box;
  background-clip: padding-box, border-box;
  border-radius: var(--radius-md);
}

/* Ensure the clickable heading inside the tile is visibly focusable */
.tile h3 a {
  text-decoration: none;
}
.tile h3 a:focus {
  outline: 2px solid #005bbb;
  outline-offset: 3px;
  border-radius: 6px;
}

/* --- 4) News list spacing (no bullets, airy) --- */
.news-list { list-style: none; margin: 0; padding: 0; }
.news-list li { padding: .4rem 0; border-bottom: 1px solid var(--line); }
.news-list li:last-child { border-bottom: 0; }

/* --- 5) Respect reduced motion --- */
@media (prefers-reduced-motion: reduce) {
  .tile { transition: none; }
}

.btn {
  display: inline-block; padding: .5rem .75rem;
  border: 1px solid var(--ucl-blue); border-radius: var(--radius-sm);
  text-decoration: none; color: var(--ucl-blue); background: transparent;
}
.btn:hover, .btn:focus {
  background: var(--ucl-blue); color: #fff; text-decoration: none;
  outline: 2px solid transparent; outline-offset: 2px;
}

.btn--primary { background: var(--ucl-blue); color: #fff; border-color: var(--ucl-blue); }
.btn--primary:hover, .btn--primary:focus { filter: brightness(0.96); }

h1, h2, h3 { color: var(--ucl-ink-2); }
h1 { font-size: clamp(1.8rem, calc(#{'1.4rem + 1.2vw'}), 2.4rem); }
h2 { font-size: clamp(1.35rem, calc(#{'1.1rem + 0.6vw'}), 1.7rem); }
h3 { font-size: 1.1rem; margin: 1rem 0 .4rem; }
p, li { color: var(--ucl-ink-2); }
.small { color: var(--muted); font-size: .95rem; }

.related-services { margin-top: 1rem; }

.input {
  padding: .5rem .6rem;
  border: 1px solid var(--line);
  border-radius: var(--radius-sm);
  width: min(28rem, 100%);
}

.benefit { display: flex; align-items: center; gap: .5rem; }
.benefit-state { display: inline-flex; width: 1.5rem; justify-content: center; }

/* LCD announcement layout */
.lcd-announcement {
  display: flex;
  flex-direction: column;    /* forces CTA to a new line */
  align-items: center;       /* center both text + CTA */
  justify-content: center;
  gap: .5rem;                /* space between text and CTA */
  text-align: center;

  /* Make sure nothing clips at various zoom levels */
  overflow: visible;
  min-height: auto;
}

/* Wrap long messages nicely */
.lcd-text {
  max-width: 65ch;           /* readable line length */
  white-space: normal;       /* allow wrapping */
  overflow-wrap: anywhere;   /* break long words/URLs if needed */
  word-break: break-word;    /* fallback for older engines */
  margin: 0;                 /* reset if inherited */
}

/* Keep the CTA centered and visible */
.lcd-cta {
  align-self: center;        /* explicit center */
  display: inline-flex;      /* consistent button sizing */
  width: auto;               /* ensure it's not squeezed */
}

/* If your LCD has a styled container, keep it flexible */
.banner .lcd-announcement,
.lcd {
  /* remove any fixed height if present */
  height: auto;
  max-height: none;
}

/* Pathway tiles grid */
.grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: 1fr;            /* default: single column */
}

/* Large screens: max two side-by-side */
@media (min-width: 1720px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* (nice-to-have) make cards stretch evenly */
.grid > .card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.card.tile h3 { margin: 0 0 .25rem; }
.card.tile p  { margin: 0; }
